* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Kanit', sans-serif;
    scroll-behavior: smooth;
}

a {
    text-decoration: none;
}

.pizza-logo {
    background: linear-gradient(to left, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0)), url(./images/img1.jpg);
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    width: 100%;
    min-height: 70vh;
}

.pizza-logo-two {
    background: linear-gradient(to right, rgba(255, 255, 255, 0.125), rgba(0, 0, 0, 0)), url(./images/logo.jpg);
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    width: 100%;
    min-height: 480px;
}

@-webkit-keyframes cartIcon {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-8px);
    }
}

@keyframes cartIcon {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-8px);
    }
}

.icon__section {
    height: 0;
    position: sticky;
    top: 87.5vh;
}

.icon__cart {
    -webkit-animation: cartIcon 1s infinite alternate;
    animation: cartIcon 1s infinite alternate;
    font-size: 70px;
    color: #e31837;
    color: yellow;
    position: sticky;
    cursor: pointer;
    outline: none;
    left: 91vw;
}

@media screen and (max-width: 767px) {
    .pizza-logo {
        background-attachment: scroll;
        min-height: 70vh;
    }
    .pizza-logo-two {
        background-attachment: scroll;
        min-height: 50vh;
    }
}