.cart {
    min-height: 100vh;
    background-color: #000;
    color: #fff;
    justify-content: center;
    height: auto;
    padding: 2.5vh 0;
    background-image: url('/src/images/cartBackground.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

.filter {
    background-image: url('/src/images/backgroundBlur.jpg');
}

@-webkit-keyframes mover {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-4px);
    }
}

@keyframes mover {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-4px);
    }
}

.cart__icon {
    -webkit-animation: mover 1.4s infinite alternate;
    animation: mover 1.4s infinite alternate;
    font-size: 62.5px;
    color: #fff;
    position: absolute;
    top: 3.5vh;
    left: 5vw;
    cursor: pointer;
    outline: none;
}

.cart__icon:hover {
    color: #ffc500;
}

.cart__heading {
    text-align: center;
    color: #fff;
    font-size: 60px;
    letter-spacing: 5px;
    padding: 0 0 2.5vh;
}

.cart__item {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
    padding: 0 0 10px;
    text-shadow: 1px 1px 1px #000;
    letter-spacing: 1px;
    word-spacing: 5px;
}

.cart__itemPrice {
    padding: 0 10px;
    font-weight: bold;
}

.cart__itemRemove {
    padding: 5px;
    color: #cf1834;
    font-weight: 100;
    font-size: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    background: transparent;
}

.cart__clear {
    color: #000;
    font-size: 1.7rem;
    padding: 0.5rem 2.7rem;
    border: none;
    outline: none;
    background: #ffc500;
    box-shadow: 6px 6px #cf1834;
    transition: .15s;
    cursor: pointer;
    display: flex;
    margin: auto;
}

.cart__clear:hover {
    color: #fff;
    background: #cf1834;
    box-shadow: 6px 6px #ffc500;
}

.cart__itemRemove:hover+.cart__item {
    color: #cf1834;
}

.cart__value {
    color: #fff;
    font-size: 30px;
    text-align: right;
    padding: 5vh 10vw 5vh 0;
    text-shadow: 1px 1px 1px #000;
}

.cart__value span {
    color: #ffc500;
}

@media screen and (max-width: 768px) {
    .cart {
        padding: 3vh 0;
        background-position: left;
    }
    .cart__icon {
        font-size: 50px;
        margin-top: 5px;
    }
    .cart__heading {
        font-size: 50px;
        letter-spacing: 3px;
        padding: 0 10vw 7.5vh;
        text-align: right;
    }
    .cart__item {
        font-size: 24px;
        word-spacing: 3px;
    }
    .cart__itemRemove {
        font-size: 26px;
        font-weight: bold;
    }
    .cart__value {
        text-align: center;
        padding: 4vh 0;
    }
    .cart__clear {
        font-size: 1.8rem;
        padding: 0.5rem 2rem;
        text-transform: uppercase;
        letter-spacing: 1px;
        word-spacing: 5px;
        margin: 2vh auto;
    }
}